import { useCallback, useEffect, useState } from 'react';
import { useAppSdk } from './appSdk';
export const useScanner = (initScan, onSubmit) => {
    const sdk = useAppSdk();
    const [scanId, setScanId] = useState(initScan);
    useEffect(() => {
        if (scanId) {
            sdk.uiEvents.emit('scan', {
                method: 'scan',
                id: scanId,
                params: undefined
            });
        }
    }, [scanId]);
    useEffect(() => {
        const handler = (options) => {
            if (options.id === scanId) {
                onSubmit(options.params);
            }
        };
        sdk.uiEvents.on('response', handler);
        return () => {
            sdk.uiEvents.off('response', handler);
        };
    }, [sdk, scanId, onSubmit]);
    return useCallback(() => {
        setScanId(Date.now());
    }, [setScanId]);
};
